<template>
  <div id="app">
		<h1>在线翻译</h1>
		<h5 class="text-muted">简单 / 易用 / 便捷</h5>
    <translateForm @formSubmit="translateText"></translateForm>
		<translateOutput v-text="Text"></translateOutput>
  </div>
</template>

<script>
import translateForm from './components/translateForm.vue'
import translateOutput from './components/translateOutput.vue'
export default {
  name: 'App',
	data(){
		return{
			Text:""
		}
	},
  components: {
    translateForm,
		translateOutput
  },
	methods:{
		translateText:function(lang,text){
			//alert(text)
			this.$axios
			.get("https://translate.yandex.net/api/v1.5/tr.json/translate?key=trnsl.1.1.20190324T084254Z.57f73d284cbe2ea3.f7d2b5d82214443679e4e5f74284d8832898564c&lang=" + lang + "&text=" + text)
			.then(response=>{
				this.Text=response.data.text[0]
			})
		}
	}
}
</script>

<style>
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #333333;
  margin-top: 60px;
	background-color: #cccccc;
	width: 100%;
	height: 100vh;
	margin: 0;
	padding-top: 60px;
}
</style>



