<!-- 输入 -->
<template>
	<div id="translateForm" class="row">
		<div class="col-md-6 col-md-offset-3 center">
			<form class="form-inline center" @submit.prevent="formSubmit">
				<input class="form-control" type="text" placeholder="请输入需要翻译的内容" v-model="textToTranslate">
				<select class="form-control" v-model="lang">
					<option value="en">English</option>
					<option value="zh">中文</option>
				</select>
				<input class="btn btn-info" type="submit" value="翻译">
			</form>
		</div>
	</div>
</template>

<script>
	export default {
		name: 'translateForm',
		data(){
	  		return{
	  			textToTranslate:"",
				lang:""
	  		}
		},
		methods:{
			formSubmit:function(){
				if(this.textToTranslate==""){
					alert("请输入需要翻译的内容");
				}else{
					this.$emit("formSubmit",this.lang,this.textToTranslate);
				}
			}
		},
		created(){
			this.lang="en";
		}
	}
</script>

<style scoped>
	#translateForm{
		
	}
	.center{
		margin: 0 auto;
	}
	.form-inline{
		width: 350px;
	}
	.form-control{
		transition:none;
	}
</style>



